.project-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: var(--space-sm) var(--space-md);
    border-radius: var(--radius-sm);
    cursor: pointer;
}

.project-item:hover {
    background: var(--color-bg);
}

.project-item.active {
    background: var(--color-tertiary);
    color: var(--color-text-light);
}

.project-delete-btn {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 14px;
    margin-left: var(--space-sm);
}

.project-item {
    cursor: pointer;
}

.project-delete-btn {
    cursor: pointer;
}

.main-content {
    flex: 1;
    background: var(--color-surface);
    padding: var(--space-lg);
    border-radius: var(--radius-md);
    display: flex;
    flex-direction: column;
    gap: var(--space-md);
    box-shadow: var(--shadow-sm);
}

.todo-card {
    border: 1px solid var(--color-border);
    border-radius: var(--radius-md);
    padding: var(--space-md);
    display: flex;
    flex-direction: column;
    gap: var(--space-sm);
    transition: var(--transition-fast);
}

.todo-row {
    display: grid;
    grid-template-columns: minmax(120px, 1fr) auto auto auto auto auto;
    gap: var(--space-sm);
    align-items: center;
}

.todo-details {
    display: none;
    font-size: var(--font-size-sm);
    color: var(--color-text-dark);
    opacity: 0.9;
}

.todo-card.expanded .todo-details {
    display: block;
}

.todo-priority {
    font-size: var(--font-size-sm);
}

.todo-priority {
    font-size: var(--font-size-sm);
    font-weight: var(--font-weight-medium);
    padding: 4px 10px;
    border-radius: 999px;
    white-space: nowrap;
}

.priority-high {
    background: rgba(244, 67, 54, 0.15);
    color: #f44336;
}

.priority-medium {
    background: rgba(255, 152, 0, 0.18);
    color: #ff9800;
}

.priority-low {
    background: rgba(76, 175, 80, 0.18);
    color: #4caf50;
}

.todo-card.completed {
    opacity: 0.55;
    background: #fafafa;
}

.todo-card.completed .todo-title {
    color: #666;
}

.todo-card.completed .todo-priority {
    filter: grayscale(0.7);
}

.todo-card.overdue {
    border-left: 4px solid #f44336;
}

.add-project-btn {
    padding: var(--space-sm) var(--space-md);
    border-radius: var(--radius-sm);
    border: 1px solid var(--color-border);
    background: var(--color-bg);
    cursor: pointer;
}

.add-todo-btn {
    padding: var(--space-sm) var(--space-md);
    border: 1px solid var(--color-border);
    background: var(--color-bg);
    border-radius: var(--radius-sm);
    cursor: pointer;
    margin-bottom: var(--space-md);
}

.todo-delete-btn {
    background: none;
    border: none;
    cursor: pointer;
    margin-left: var(--space-sm);
    font-size: 16px;
}

.todo-edit-btn,
.todo-delete-btn {
    opacity: 0.5;
    transition: opacity var(--transition-fast);
}

.todo-edit-btn:hover,
.todo-delete-btn:hover {
    opacity: 1;
}

#app-modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: none;
    padding: var(--space-lg);
    border-radius: var(--radius-md);
    box-shadow: var(--shadow-lg);
    background: var(--color-surface);
    display: flex;
    flex-direction: column;
    gap: var(--space-md);
    min-width: 350px;
}

/* Center modal */
#app-modal::backdrop {
    background: rgba(0, 0, 0, 0.3);
    backdrop-filter: blur(2px);
}

/* Form layout */
#app-modal form {
    display: flex;
    flex-direction: column;
    gap: var(--space-md);
}

/* Inputs */
#app-modal input,
#app-modal textarea,
#app-modal select {
    padding: var(--space-sm);
    border: 1px solid var(--color-border);
    border-radius: var(--radius-sm);
    font-size: var(--font-size-base);
}

/* Buttons row */
#app-modal form div {
    display: flex;
    justify-content: space-between;
    gap: var(--space-sm);
}

#app-modal button {
    padding: var(--space-sm) var(--space-md);
    border-radius: var(--radius-sm);
    border: none;
    cursor: pointer;
}

#app-modal button[type="submit"] {
    background: var(--color-tertiary);
    color: var(--color-text-light);
}

#app-modal button[type="button"] {
    background: var(--color-border);
    color: var(--color-text-dark);
}

#app-modal:not([open]) {
    display: none;
}

#app-modal textarea {
    resize: vertical;
    /* or 'none' if you prefer */
    min-height: 70px;
}

#app-modal input,
#app-modal select,
#app-modal textarea {
    width: 100%;
}

.project-rename-btn,
.project-delete-btn {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 14px;
    padding: 0 4px;
}

.project-rename-btn,
.project-delete-btn {
    opacity: 0.6;
}

.project-rename-btn:hover,
.project-delete-btn:hover {
    opacity: 1;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;;AAEA;IACI,iBAAiB;IACjB,wBAAwB;IACxB,cAAc;IACd,0BAA0B;IAC1B,eAAe;IACf,yBAAyB;IACzB,iBAAiB;;IAEjB,aAAa;IACb,mBAAmB;IACnB,kBAAkB;IAClB,wBAAwB;IACxB,mBAAmB;IACnB,0BAA0B;IAC1B,2BAA2B;IAC3B,uBAAuB;;IAEvB,eAAe;IACf,8DAA8D;IAC9D,sBAAsB;IACtB,oBAAoB;IACpB,oBAAoB;IACpB,uBAAuB;IACvB,yBAAyB;IACzB,yBAAyB;IACzB,uBAAuB;;IAEvB,kBAAkB;IAClB,eAAe;IACf,eAAe;IACf,gBAAgB;IAChB,gBAAgB;IAChB,gBAAgB;;IAEhB,qBAAqB;IACrB,gBAAgB;IAChB,gBAAgB;IAChB,iBAAiB;;IAEjB,0CAA0C;IAC1C,0CAA0C;IAC1C,2CAA2C;;IAE3C,eAAe;IACf,6BAA6B;IAC7B,+BAA+B;AACnC;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,gBAAgB;AACpB;;AAEA;IACI,+BAA+B;IAC/B,iCAAiC;IACjC,6BAA6B;IAC7B,oCAAoC;IACpC,gCAAgC;IAChC,aAAa;IACb,sBAAsB;IACtB,OAAO;AACX;;AAEA;IACI,aAAa;IACb,OAAO;IACP,sBAAsB;AAC1B;;AAEA;IACI,sCAAsC;IACtC,8BAA8B;IAC9B,wBAAwB;IACxB,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,iBAAiB;IACjB,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,oBAAoB;AACxB;;AAEA;IACI,uCAAuC;IACvC,8BAA8B;IAC9B,YAAY;IACZ,wCAAwC;IACxC,eAAe;IACf,+BAA+B;IAC/B,gCAAgC;IAChC,oCAAoC;AACxC;;AAEA;IACI,wCAAwC;AAC5C;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,OAAO;AACX;;AAEA;IACI,sCAAsC;IACtC,8BAA8B;IAC9B,kBAAkB;IAClB,wBAAwB;AAC5B;;AAEA;IACI,aAAa;IACb,OAAO;IACP,uBAAuB;IACvB,mBAAmB;IACnB,gBAAgB;IAChB,yDAAkD;IAClD,sBAAsB;IACtB,2BAA2B;AAC/B;;AAEA;IACI,gCAAgC;IAChC,wCAAwC;IACxC,+BAA+B;IAC/B,kBAAkB;IAClB,4BAA4B;AAChC;;AAEA;IACI,oCAAoC;IACpC,8BAA8B;AAClC;;AAEA;IACI,8BAA8B;AAClC;;AAEA;IACI,wCAAwC;IACxC,+BAA+B;IAC/B,iCAAiC;IACjC,8BAA8B;IAC9B,sCAAsC;IACtC,kCAAkC;IAClC,YAAY;AAChB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,sBAAsB;AAC1B;;AAEA,qBAAqB;;AAErB;IACI,aAAa;IACb,OAAO;IACP,oBAAoB;IACpB,wBAAwB;AAC5B;;AAEA;IACI,YAAY;IACZ,gCAAgC;IAChC,2CAA2C;IAC3C,wBAAwB;IACxB,aAAa;IACb,sBAAsB;IACtB,oBAAoB;IACpB,4BAA4B;AAChC;;AAEA;;;;;;;;;;;;;;;GAeG;;AAEH;IACI,aAAa;IACb,mBAAmB;IACnB,8BAA8B;IAC9B,wCAAwC;IACxC,+BAA+B;IAC/B,eAAe;AACnB;;AAEA;IACI,2BAA2B;AAC/B;;AAEA;IACI,