d-color: var(--color-tertiary);\n    color: var(--color-text-light);\n    border: none;\n    padding: var(--space-sm) var(--space-md);\n    cursor: pointer;\n    border-radius: var(--radius-sm);\n    font-size: var(--font-size-base);\n    transition: var(--transition-normal);\n}\n\nheader .navbar button:hover{\n    background-color: var(--color-secondary);\n}\n\nheader .navbar button:active{\n    transform: scale(0.98);\n}\n\n.content{\n    display: flex;\n    flex: 1;\n}\n\nfooter{\n    background-color: var(--color-primary);\n    color: var(--color-text-light);\n    text-align: center;\n    padding: var(--space-md);\n}\n\n#home-page {\n    display: flex;\n    flex: 1;\n    justify-content: center;\n    align-items: center;\n    min-height: 80vh;\n    background-image: url('./assets/images/hero.webp');\n    background-size: cover;\n    background-position: center;\n}\n\n#home-page .card {\n    background: var(--color-surface);\n    padding: var(--space-lg) var(--space-xl);\n    border-radius: var(--radius-md);\n    text-align: center;\n    box-shadow: var(--shadow-md);\n}\n\n#home-page .card h2 {\n    font-weight: var(--font-weight-bold);\n    margin-bottom: var(--space-sm);\n}\n\n#home-page .card p {\n    margin-bottom: var(--space-md);\n}\n\n#home-page .card button {\n    padding: var(--space-sm) var(--space-lg);\n    border-radius: var(--radius-sm);\n    background: var(--color-tertiary);\n    color: var(--color-text-light);\n    font-weight: var(--font-weight-medium);\n    transition: var(--transition-fast);\n    border: none;\n}\n\n#home-page .card button:hover {\n    background: #4151ff;\n}\n\n#home-page .card button:active {\n    transform: scale(0.97);\n}\n\n/* Application page */\n\n#application-page {\n    display: flex;\n    flex: 1;\n    gap: var(--space-md);\n    padding: var(--space-md);\n}\n\n.sidebar {\n    width: 230px;\n    background: var(--color-surface);\n    border-right: 1px solid var(--color-border);\n    padding: var(--space-md);\n    display: flex;\n    flex-direction: column;\n    gap: var(--space-md);\n    box-shadow: var(--shadow-sm);\n}\n\n/* .project-item {\n    padding: var(--space-sm) var(--space-md);\n    border-radius: var(--radius-sm);\n    cursor: pointer;\n    transition: background var(--transition-fast);\n}\n\n.project-item:hover {\n    background: var(--color-bg);\n}\n\n.project-item.active {\n    background: var(--color-tertiary);\n    color: var(--color-text-light);\n    font-weight: var(--font-weight-medium);\n} */\n\n.project-item {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    padding: var(--space-sm) var(--space-md);\n    border-radius: var(--radius-sm);\n    cursor: pointer;\n}\n\n.project-item:hover {\n    background: var(--color-bg);\n}\n\n.project-item.active {\n    background: var(--color-tertiary);\n    color: var(--color-text-light);\n}\n\n.project-delete-btn {\n    background: none;\n    border: none;\n    cursor: pointer;\n    font-size: 14px;\n    margin-left: var(--space-sm);\n}\n\n.project-item {\n    cursor: pointer;\n}\n\n.project-delete-btn {\n    cursor: pointer;\n}\n\n.main-content {\n    flex: 1;\n    background: var(--color-surface);\n    padding: var(--space-lg);\n    border-radius: var(--radius-md);\n    display: flex;\n    flex-direction: column;\n    gap: var(--space-md);\n    box-shadow: var(--shadow-sm);\n}\n\n.todo-card {\n    border: 1px solid var(--color-border);\n    border-radius: var(--radius-md);\n    padding: var(--space-md);\n    display: flex;\n    flex-direction: column;\n    gap: var(--space-sm);\n    transition: var(--transition-fast);\n}\n\n.todo-row {\n    display: grid;\n    grid-template-columns: minmax(120px, 1fr) auto auto auto auto auto;\n    gap: var(--space-sm);\n    align-items: center;\n}\n\n.todo-details {\n    display: none;\n    font-size: var(--font-size-sm);\n    color: var(--color-text-dark);\n    opacity: 0.9;\n}\n\n.todo-card.expanded .todo-details {\n    display: block;\n}\n\n.todo-priority {\n    font-size: var(--font-size-sm);\n}\n\n.todo-priority {\n    font-size: var(--font-size-sm);\n    font-weight: var(--font-weight-medium);\n    padding: 4px 10px;\n    border-radius: 999px;\n    white-space: nowrap;\n}\n\n.priority-high {\n    background: rgba(244, 67, 54, 0.15);\n    color: #f44336;\n}\n\n.priority-medium {\n    background: rgba(255, 152, 0, 0.18);\n    color: #ff9800;\n}\n\n.priority-low {\n    background: rgba(76, 175, 80, 0.18);\n    color: #4caf50;\n}\n\n.todo-card.completed {\n    opacity: 0.55;\n    background: #fafafa;\n}\n\n.todo-card.completed .todo-title {\n    color: #666;\n}\n\n.todo-card.completed .todo-priority {\n    filter: grayscale(0.7);\n}\n\n.todo-card.overdue {\n    border-left: 4px solid #f44336;\n}\n\n.add-project-btn {\n    padding: var(--space-sm) var(--space-md);\n    border-radius: var(--radius-sm);\n    border: 1px solid var(--color-border);\n    background: var(--color-bg);\n    cursor: pointer;\n}\n\n.add-todo-btn {\n    padding: var(--space-sm) var(--space-md);\n    border: 1px solid var(--color-border);\n    background: var(--color-bg);\n    border-radius: var(--radius-sm);\n    cursor: pointer;\n    margin-bottom: var(--space-md);\n}\n\n.todo-delete-btn {\n    background: none;\n    border: none;\n    cursor: pointer;\n    margin-left: var(--space-sm);\n    font-size: 16px;\n}\n\n.todo-edit-btn,\n.todo-delete-btn {\n    opacity: 0.5;\n    transition: opacity var(--transition-fast);\n}\n\n.todo-edit-btn:hover,\n.todo-delete-btn:hover {\n    opacity: 1;\n}\n\n#app-modal {\n    position: fixed;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n    border: none;\n    padding: var(--space-lg);\n    border-radius: var(--radius-md);\n    box-shadow: var(--shadow-lg);\n    background: var(--color-surface);\n    display: flex;\n    flex-direction: column;\n    gap: var(--space-md);\n    min-width: 350px;\n}\n\n/* Center modal */\n#app-modal::backdrop {\n    background: rgba(0, 0, 0, 0.3);\n    backdrop-filter: blur(2px);\n}\n\n/* Form layout */\n#app-modal form {\n    display: flex;\n    flex-direction: column;\n    gap: var(--space-md);\n}\n\n/* Inputs */\n#app-modal input,\n#app-modal textarea,\n#app-modal select {\n    padding: var(--space-sm);\n    border: 1px solid var(--color-border);\n    border-radius: var(--radius-sm);\n    font-size: var(--font-size-base);\n}\n\n/* Buttons row */\n#app-modal form div {\n    display: flex;\n    justify-content: space-between;\n    gap: var(--space-sm);\n}\n\n#app-modal button {\n    padding: var(--space-sm) var(--space-md);\n    border-radius: var(--radius-sm);\n    border: none;\n    cursor: pointer;\n}\n\n#app-modal button[type=\"submit\"] {\n    background: var(--color-tertiary);\n    color: var(--color-text-light);\n}\n\n#app-modal button[type=\"button\"] {\n    background: var(--color-border);\n    color: var(--color-text-dark);\n}\n\n#app-modal:not([open]) {\n    display: none;\n}\n\n#app-modal textarea {\n    resize: vertical;\n    /* or 'none' if you prefer */\n    min-height: 70px;\n}\n\n#app-modal input,\n#app-modal select,\n#app-modal textarea {\n    width: 100%;\n}\n\n.project-rename-btn,\n.project-delete-btn {\n    background: none;\n    border: none;\n    cursor: pointer;\n    font-size: 14px;\n    padding: 0 4px;\n}\n\n.project-rename-btn,\n.project-delete-btn {\n    opacity: 0.6;\n}\n\n.project-rename-btn:hover,\n.project-delete-btn:hover {\n    opacity: 1;\n}"],"sourceRoot":""}]);
